.header {
    margin-top: 150px;
    text-align: center;
    text-transform: uppercase;
    @include mq-sm {
        margin-top: 75px; }

    @include mq-xs {
        margin-top: 50px; }

    &__title {
        font-size: 72px;
        color: #333;
        margin-bottom: 20px;

        @include mq-xs {
            font-size: 36px; } }


    &__subtitle {
        font-size: 20px;
        color: #868686;
        margin-top: 0px;

        @include mq-xs {
            font-size: 16px; } } }
